.App2 {
    text-align: center;
    background: rgb(2,0,36);
    background: linear-gradient(90deg, rgba(2,0,36,1) 0%, rgba(40,44,52,1) 35%, rgba(0,212,255,1) 100%);
   
  }
a {
    /* marker: none; */
    
    text-decoration: none;
}

.footers{

    color: whitesmoke
}
  
  .titulo2{
  
    word-break: break-word;
    overflow-wrap: break-word;
    color: #282c34;
    text-align: center;
    margin-bottom: 10px;
    margin-right: 1em;
  }
  
  .padresito2{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: center;
    justify-content:space-evenly;
    
  }
  
  @keyframes balanceo {
    0% { transform: rotate(0deg); }
    50% { transform: rotate(3deg); }
    100% { transform: rotate(-5deg); }
  }
  
  h1{
    
   
    font-size: 23px;
    margin-bottom: 10px;
  }
  
  .floating2{
    position: absolute;
    margin-left: -707px;
    margin-top: -120px;
    
  
  
  
  }
  
  .floating2:hover{
  
    transform: rotate(-10deg);
    transition-duration: 3s;
    transform: translateX(300px);
    animation: balanceo 2s infinite alternate;
  }
  
  .logos2:hover{
    transform: rotate(-10deg);
    transition-duration: 3s;
    transform: translateX(300px);
    
    animation: balanceo 2s infinite alternate;
  
  }
  
  
  img {
    transition: all 0.6s ease;
    border-radius: 8px;
   
  
    
  }
  
  
  p{
  
  
    color: #282c34;
  }
  
  h1{
  
    color: #282c34;
  
  
  }
  
  
  .hijitos2{
    
    margin-bottom: 50px;
    width: 100vh;
  
  }
  .superpadre2{
    
  
    margin-left: 5%;
    margin-right: 5%;
  }
  
  
  .imagenprincipal{

    margin-top: 2rem;
    border: 0.3rem solid #11b0c8;

  }
  
  
  .App-logo2 {
    height: 40vmin;
    pointer-events: none;
    
  }
  
  
  @media (max-width: 370px) {
    h1 {
      font-size: 35px;
    }
    .App-logo2 {
      height: 170px;
      pointer-events: none;
      margin: 0;
      padding: 0;
    }
  
    .padresito2{
      display: block;
      
      
    }
    
    .floating2{
      display: none;
     
    
    
    
    }
  }
  
  @media (prefers-reduced-motion: no-preference) {
    .App-logo2{
      animation: App-logo-spin infinite 20s linear;
    }
  }
  
  .App-header2 {
    background-color: transparent;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
    
  }
  
  .App-link2 {
    color: #61dafb;
  }
  
  @keyframes App-logo2-spin {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(360deg);
    }
  }
  
  
  .h3 h1 h2 p {
  
  color: #282c34;
  margin-bottom: 10px;
  
  }
  
  
  
  .app-card-list2 {
    display:flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    align-content: center;
    width:100%;
   
    min-height: 75vh;
    white-space:normal;
    overflow-x: scroll;
   
    
    &::-webkit-scrollbar { 
      display: none; 
    }
    
   
    
    .card2 {
      margin: 60px 30px;
      display: inline-block;
      color: aliceblue;
      
    }
  }
  .card-body2 {
    white-space: normal;
    background: whitesmoke;
    box-shadow: 5px 5px 20px #060526;
    border-radius: 8px;
  
  }
  
  
  .card-body2:hover {
    
    box-shadow: 5px 5px 20px white;
    
    
  }
  
  
  .detalles{

  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;

  }

.titulonombre{

 font-size: 3rem;

