.App {
  text-align: center;
  background: rgb(2,0,36);
  background: linear-gradient(90deg, rgba(2,0,36,1) 0%, rgba(40,44,52,1) 35%, rgba(0,212,255,1) 100%);
 
}

.titulo{

  word-break: break-word;
  overflow-wrap: break-word;
  color: #282c34;
  text-align: center;
  margin-bottom: 10px;
}

.padresito{
 
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  justify-content:space-evenly;
  
}
.close{

 position: absolute;
 right: 1%;
 top:  1%;
}

@keyframes balanceo {
  0% { transform: rotate(0deg); }
  50% { transform: rotate(3deg); }
  100% { transform: rotate(-5deg); }
}

h1{
  
 
  font-size: 23px;
  margin-bottom: 10px;
}

.floating{
  position: absolute;
  margin-left: -707px;
  margin-top: -130px;
  z-index: 100;


}

.floating:hover{

  transform: rotate(-10deg);
  transition-duration: 3s;
  transform: translateX(300px);
  animation: balanceo 2s infinite alternate;
}

.logos:hover{
  transform: rotate(-10deg);
  transition-duration: 3s;
  transform: translateX(300px);
  
  animation: balanceo 2s infinite alternate;

}


img {
  transition: all 0.6s ease;
  border-radius: 8px;

  
}

.search{

 margin-bottom: 3rem;
 position: relative;
}

.search input{

  height: 3rem;
  width: 20rem;
  border-color: #11b0c8;
 
 }

 .search button{


    position: absolute;
    top: 0%;
    height: 53px;
 }





p{


  color: #282c34;
}

h1{

  color: #282c34;


}


.hijitos{
  
  margin-bottom: 50px;
  position: relative;
}
.superpadre{
  

  margin-left: 5%;
  margin-right: 5%;
}





.App-logo {
  height: 40vmin;
  pointer-events: none;
 
  
}


@media (max-width: 370px) {
  h1 {
    font-size: 35px;
  }
  .App-logo {
    height: 170px;
    pointer-events: none;
    margin: 0;
    padding: 0;
  }

  .padresito{
    display: block;
    
    
  }
  
  .floating{
    display: none;
   
  
  
  
  }

  .search input {
    height: 3rem;
    width: 11rem;
    
}
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: transparent;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


.h3 h1 h2 p {

color: #282c34;
margin-bottom: 10px;

}



.app-card-list {
  display:flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  align-content: center;
  width:100%;
 
  min-height: 75vh;
  white-space:normal;
  overflow-x: scroll;
 
  
  &::-webkit-scrollbar { 
    display: none; 
  }
  
 
  
  .card {
    margin: 60px 30px;
    display: inline-block;
    color: aliceblue;
    
  }
}
.card-body {
  white-space: normal;
  background: whitesmoke;
  box-shadow: 5px 5px 20px #060526;
  border-radius: 8px;

}


.card-body:hover {
  
  box-shadow: 5px 5px 20px white;
  
  
}


